// import { getTrainDetail } from '@/common/request.js'
export default {
	// components:{
	// 	getTrainDetail
	// },
	data() {
		return {
			trainNo:1,
			trainDetail:''
		}
	},
	onLoad(options) {
		console.log("传过来的值");
		console.log(options.id);
		this.trainNo = options.id;
		uni.setNavigationBarTitle({
		    title: '详情信息'
		});
		this.getTrainDetails(options.id);
	},
	
	methods: {
		async  post(url, data) {
			return new Promise(function(resolve, reject) {
				uni.request({
					url: getApp().globalData.hosturl + url,
					method: 'POST',
					header: {
						"content-type": 'application/x-www-form-urlencoded'
					},
					data,
					success: function(data) {
						resolve(data)
					},
					fail: function(err) {
						// 回调失败时
						console.log(err)
						if (typeof reject == 'function') {
							reject(data);
						} else {
							console.log(data);
						}
					}
				})
			})
		},
		getTrainDetail(id) { // 参数：id
			return this.post('/id', {
				id
			})
		},
		async getTrainDetails(id){
		 let data =	await this.getTrainDetail(id);
		 console.log("run...");
		 console.log(data);
		 console.log(data.data.code);
		 if (data.data.code == 200) {
			
		 	this.trainDetail = data.data.data;
			console.log(data.data)
		 
		 }
		},
	}
}
